os de Encogimiento")])]),_c('p',{staticClass:"mb-4"},[_c('strong',[_vm._v("El encogimiento y sus causas se pueden separar en 2 tipos:")]),_vm._v(" encogimiento por construcción y encogimiento por proceso. Significa que el encogimiento se ve afectado tanto por los parámetros de construcción de la tela y las fuerzas aplicadas durante el teñido y el acabado. Los procesos de costura y acabados durante la confección también pueden afectar el encogimiento. El encogimiento de un producto se ve afectado tanto por la aplicación y remoción de factores de estrés (Cottonworks, s.f.).")]),_c('div',{staticClass:"cajon color-acento-contenido p-4 mb-4"},[_c('p',{staticClass:"mb-0"},[_vm._v("Dentro de los acabados, los expuestos en la sección de acabados en húmedo, aportan a la estabilización de las dimensiones de la tela, ayudando así al manejo del encogimiento textil.")])]),_c('p',{staticClass:"mb-0"},[_vm._v("Estos acabados son algunos de los más comunes y se pueden dar en tejidos o prendas. Para el patronista es relevante esta información pues al momento de desarrollar los patrones debe contemplar el efecto del proceso en la construcción de la prenda y los cambios dimensionales para hacer las correcciones necesarias en la moldería.")])])])])],1)])])],1)],1)}
