r el patrón de la prenda a confeccionar.")]),_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-md-3"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1-imagen6.svg")}})])]),_c('div',{staticClass:"col-md-8 offset-md-1"},[_c('div',{staticClass:"cajon color-primario p-5 mb-4"},[_c('p',{staticClass:"mb-0"},[_vm._v("Con el fin de agilizar el proceso de patronaje industrializado y evitar construir un patrón para cada talla, se recurre al proceso de escalado, que consiste en aumentar o disminuir las medidas de cada una de las piezas según el tamaño o talla del cuerpo al que corresponde.")])]),_c('p',{staticClass:"mb-0"},[_vm._v("Una vez finalizados los patrones, dependiendo del ancho útil de la tela, se realiza un trazado preliminar con el fin de calcular el consumo de materiales y registrar la información en la ficha técnica del producto, la cual será suministro de las áreas financieras y de producción.")])])]),_c('figure',{staticClass:"mt-2 mb-5"},[_c('figcaption',[_vm._v("Fuente: Técnicas de patronaje. Tomo I: Mujer (Carrera de Diseño y Gestión en Moda, 2015a)")])]),_c('figure',{staticClass:"mb-5"},[_c('img',{attrs:{"src":require("@/assets/curso/tema1-imagen7.jpg")}}),_c('figcaption',[_vm._v("Fuente: Elaboración propia en Optitex Marker.")])]),_c('p',{staticClass:"mb-4"},[_vm._v("En definitiva, el patronaje está presente en toda la cadena productiva del vestuario, iniciando con la creación de los patrones a partir del concepto creativo del diseñador, conservando su originalidad, asegurando las correctas medidas y proporciones, suministrando la información necesaria para los cálculos financieros y productivos, constituyéndose en un aliado fundamental que enlaza el departamento creativo con el de producción.")])]),_c('div',{staticClass:"col-md-8"},[_c('div',{staticClass:"cajon-adicional--con-bordes px-3 py-2 mb-5"},[_c('p',{staticClass:"mb-0"},[_vm._v("(Adaptado de: Carrera de Diseño y Gestión en Moda, 2015a).")])])])])},func